@import './variables';

/* Todo 앱 스타일 */
.App {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;

  /* 기본값, JavaScript에서 window.innerHeight로 덮어씀 */
  margin: 0 auto;
  overflow: hidden;
  min-width: 360px;

  /* 데스크톱 */
  &.desktop {
    overflow: hidden;

    .nav {
      right: auto;
      left: auto;
      width: 100%;
    }

    .bottom-sheet {
      width: 100%;
      animation: slide-up-desktop 0.3s cubic-bezier(0.32, 0.72, 0, 1);

      &.closing {
        animation: slide-down-desktop 0.3s cubic-bezier(0.32, 0.72, 0, 1);
      }
    }

    // .cont-todolist {
    //   max-height: calc(85vh - 100px);
    // }

    .side-menu {
      border-radius: $border-radius-xl 0 0 $border-radius-xl;

      &.open {
        right: 0;
      }
    }
  }

  /* 모바일 - 전체 화면 */
  &.mobile {
    .top-header {
      padding: 20px 16px 12px;
      padding-top: max(20px, env(safe-area-inset-top, 0));
      border-radius: 0;
    }

    .cont-nav {
      padding: 8px 12px;
    }

    .menu-overlay,
    .side-menu {
      position: fixed;
    }

    .side-menu {
      height: 100dvh;
    }
  }
}

/* 메뉴 오버레이 */
.menu-overlay {
  position: absolute;
  inset: 0;
  z-index: 150;
  background: rgb(255 255 255 / 0.05%);
  animation: fade-in 0.3s ease;
  backdrop-filter: blur(3px);
}

/* 사이드 메뉴 */
.side-menu {
  position: absolute;
  top: 0;
  right: -100%;
  bottom: 59px;
  z-index: 9999;
  display: flex;
  flex-direction: column;
  width: 80%;
  background: $bg-white;
  border-radius: $border-radius-xl 0 0 $border-radius-xl;
  overflow: hidden;
  box-shadow: -4px 0 20px rgb(0 0 0 / 15%);
  text-align: center;

  &.open {
    right: 0;
    animation: slide-menu-in 0.4s cubic-bezier(0.32, 0.72, 0, 1);
  }

  &.closing {
    right: -100%;
    animation: slide-menu-out 0.4s cubic-bezier(0.32, 0.72, 0, 1);
  }
}

@keyframes slide-menu-in {
  from {
    right: -50%;
  }

  to {
    right: 0;
  }
}

@keyframes slide-menu-out {
  from {
    right: 0;
  }

  to {
    right: -100%;
  }
}

.side-menu-header {
  display: flex;
  align-items: center;
  justify-content: end;
  height: 60px;
  padding: 16px 20px;
  color: $bg-white;
}

/* 사용자 정보 */
.user-info-container {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 16px 20px;
}

.user-thumbnail {
  position: relative;
  flex-shrink: 0;
  width: 40px;
  height: 40px;
  border: 1px solid $input-color;
  border-radius: 10px;
  overflow: hidden;
}

.user-thumbnail-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.user-thumbnail-default {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: $bg-white;
}

.user-thumbnail-icon {
  width: auto;
  height: 18px;
  object-fit: contain;
  opacity: 0.5;
}

.user-info-text {
  display: flex;
  flex: 1;
  flex-direction: column;
  gap: 2px;
  overflow: hidden;
  text-align: left;
}

.user-nickname {
  margin: 0;
  color: $text-dark;
  font-size: 14px;
  font-weight: 600;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.user-email {
  margin: 0;
  color: $text-medium;
  font-size: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.close-menu-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  padding: 0;
  background: transparent;
  border: none;
  border-radius: 50%;
  color: $primary-color;
  font-size: 24px;
  cursor: pointer;
  transition: $transition-base;

  &:hover {
    background: rgb(255 255 255 / 20%);
  }

  &:active {
    transform: scale(0.9);
  }
}

.side-menu-nav {
  flex: 1;
  padding: 12px 0;
  overflow-y: auto;

  ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 12px;
    padding: 14px 24px;
    color: $text-dark;
    font-family: "Noto Sans KR", Roboto, sans-serif;
    font-size: 13px;
    font-weight: 500;
    cursor: pointer;
    transition: $transition-base;

    svg {
      color: $text-light;
      transition: $transition-base;
    }

    &:hover {
      background: rgb(43 174 102 / 10%);

      svg {
        color: $secondary-color;
        transform: translateX(2px);
      }
    }

    &:active {
      background: rgb(43 174 102 / 20%);
    }
  }
}

/* 사이드 메뉴 - 상세 페이지 */
.side-menu-header.detail {
  justify-content: space-between;
  height: 80px;

  h2 {
    position: absolute;
    left: 50%;
    margin: 0;
    color: $text-dark;
    font-family: "Fjalla One", sans-serif;
    font-size: 16px;
    font-weight: 700;
    transform: translateX(-50%);
  }
}

.back-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  padding: 0;
  background: transparent;
  border: none;
  border-radius: 50%;
  color: $primary-color;
  cursor: pointer;
  transition: $transition-base;

  &:hover {
    background: rgb(43 174 102 / 10%);
  }

  &:active {
    transform: scale(0.9);
  }
}

.side-menu-detail {
  flex: 1;
  padding: 12px 0 80px;
  overflow-y: auto;
}

.option-list {
  margin: 0;
  padding: 0;
  list-style: none;

  li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px 24px;
    color: $text-dark;
    font-family: "Noto Sans KR", Roboto, sans-serif;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: $transition-base;

    svg {
      color: $secondary-color;
      flex-shrink: 0;
    }

    &:hover {
      background: rgb(43 174 102 / 5%);
    }

    &:active {
      background: rgb(43 174 102 / 10%);
    }

    &.active {
      color: $secondary-color;
      font-weight: 600;
    }
  }
}

.tag-list {
  margin: 0;
  padding: 0;
  list-style: none;

  .tag-item {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px 24px;
    border-bottom: 1px solid rgb(0 0 0 / 5%);
    transition: $transition-base;

    &:last-child {
      border-bottom: none;
    }

    &:hover {
      background: rgb(6 48 100 / 3%);
    }
  }

  .tag-item-left {
    position: relative;
    display: flex;
    align-items: center;
    gap: 8px;
  }

  .tag-item-right {
    position: relative;
    display: flex;
    align-items: center;
    gap: 12px;
  }

  .tag-name {
    position: relative;
    z-index: 9;
    color: $text-dark;
    font-family: "Noto Sans KR", Roboto, sans-serif;
    font-size: 14px;
    font-weight: 600;
  }

  .tag-name-input {
    width: 100%;
    padding: 6px 8px;
    background: $bg-white;
    border: 1px solid $input-color;
    border-radius: 4px;
    color: $text-dark;
    font-family: inherit;
    font-size: 12px;
    transition: $transition-base;
    resize: none;

    &::placeholder {
      color: $text-gray;
    }

    &:focus {
      background: $bg-white;
      border-color: $border-dark;
      outline: none;
    }
  }

  .tag-edit-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 6px;
    background: transparent;
    border: none;
    border-radius: 4px;
    color: $text-medium;
    cursor: pointer;
    transition: $transition-base;

    svg {
      width: 14px;
      height: 14px;
    }

    &:hover {
      background: rgb(6 48 100 / 8%);
      color: $primary-color;
    }
  }

  .tag-count {
    color: $text-medium;
    font-family: "Noto Sans KR", Roboto, sans-serif;
    font-size: 12px;
    font-weight: 500;
  }

  .tag-color-btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 6px;
    background: transparent;
    border: none;
    border-radius: 4px;
    color: $text-medium;
    cursor: pointer;
    transition: $transition-base;

    .tag-color-btn-icon {
      width: 3px;
      height: 3px;
      background: $text-medium;
      border-radius: 50%;

      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 50%;
        width: 3px;
        height: 3px;
        background: $text-medium;
        transform: translateX(-50%);
        border-radius: 50%;
      }

      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 50%;
        width: 3px;
        height: 3px;
        background: $text-medium;
        transform: translateX(-50%);
        border-radius: 50%;
      }
    }

    &:hover {
      background: rgb(0 0 0 / 5%);
      color: $text-dark;
    }

    svg {
      display: block;
    }
  }

  .empty-tag-message {
    padding: 48px 24px;
    color: $text-medium;
    font-family: "Noto Sans KR", Roboto, sans-serif;
    font-size: 14px;
    text-align: center;
  }
}

/* 색상 선택 팝업 */
.color-picker-overlay {
  position: fixed;
  inset: 0;
  z-index: 300;
  background: transparent;
}

.color-picker-popup {
  position: absolute;
  top: calc(100% + 4px);
  right: 15px;
  z-index: 99999;
  min-width: 180px;
  padding: 16px;
  background: $bg-white;
  border: 1px solid $input-color;
  border-radius: $border-radius-md;
  box-shadow: 0 8px 24px rgb(0 0 0 / 15%);
}

.color-picker-title {
  margin: 0 0 12px;
  padding: 0;
  color: $text-dark;
  font-family: "Noto Sans KR", Roboto, sans-serif;
  font-size: 12px;
  font-weight: 600;
  text-align: left;
}

.color-picker-grid {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.color-option {
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
  width: 100%;
  padding: 8px 10px;
  background: transparent;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: $transition-base;

  &:hover {
    background: rgb(6 48 100 / 5%);
  }

  &.selected {
    background: rgb(6 48 100 / 8%);
  }
}

.color-circle {
  width: 14px;
  height: 14px;
  border: 2px solid rgb(0 0 0 / 10%);
  border-radius: 50%;
  flex-shrink: 0;
}

.color-name {
  flex: 1;
  color: $text-dark;
  font-family: "Noto Sans KR", Roboto, sans-serif;
  font-size: 11px;
  font-weight: 500;
  text-align: left;
}

.color-check {
  flex-shrink: 0;
  color: $primary-color;
}

.side-menu-footer {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9999;
  padding: 24px 20px 48px;
  background: $bg-white;

  p {
    margin: 0;
    color: #999;
    font-family: Roboto, sans-serif;
    font-size: 11px;
    font-weight: 400;
  }
}

/* 상단 헤더 바 */
.top-header {
  position: relative;
  z-index: 100;
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: space-between;
  padding: 27px 27px 21px;
  padding-top: calc(27px + env(safe-area-inset-top, 0));
  background: rgb(255 255 255 / 98%);
}

.todolist-header {
  position: absolute;
  top: 0;
  right: 30px;
  z-index: 10;
}

.menu-btn {
  position: relative;
  z-index: 10;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 40px;
  height: 40px;
  padding: 8px;
  background: transparent;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: $transition-base;

  &:hover {
    background: rgb(43 174 102 / 10%);
  }

  &:active {
    transform: scale(0.95);
  }

  .menu-btn-icon {
    position: relative;
    width: 24px;
    height: 2px;
    background: $primary-color;
    border-radius: 2px;
    transition: $transition-base;

    &::before {
      position: absolute;
      top: 4px;
      right: 0;
      width: 11px;
      height: 2px;
      background: $primary-color;
      border-radius: 2px;
      content: "";
      transition: $transition-base;
    }

    &::after {
      position: absolute;
      top: 4px;
      right: 12px;
      width: 7px;
      height: 2px;
      background: $primary-color;
      border-radius: 2px;
      content: "";
      transition: $transition-base;
    }
  }
}

.app-title {
  display: flex;
  flex: 1;
  align-items: center;
  gap: 3px;
  margin: 0;
  color: $text-dark;
  font-family: "Fjalla One", sans-serif;
  font-size: 24px;
  font-weight: 800;
  letter-spacing: 1.5px;
}

.app-logo {
  width: 15px;
  height: 18px;
  object-fit: contain;
  transform: rotate(10deg);
  margin: 0 0 -5px 2px;
}

.write-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  padding: 0;
  background: transparent;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: $transition-base;

  &:hover {
    background: rgb(43 174 102 / 10%);
    transform: scale(1.05);
  }

  &:active {
    transform: scale(0.95);
  }
}

.write-icon {
  width: 24px;
  height: 24px;
  object-fit: contain;
}

.header-spacer {
  width: 28px;
}

.container {
  position: relative;
  display: flex;
  flex: 1 1 0;
  flex-direction: column;

  // min-height: 0;
  background: rgb(255 255 255 / 95%);
  overflow: hidden;
  backdrop-filter: blur(20px);
  box-shadow: 0 8px 32px rgb(31 38 135 / 15%);
  transform: translateZ(0);
}

/* 날짜 네비게이션 */
.date-navigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 24px;
  background: $bg-white;
  border-bottom: 1px solid $border-light;
}

.date-nav-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  padding: 0;
  background: transparent;
  border: none;
  border-radius: 50%;
  color: $primary-color;
  cursor: pointer;
  transition: $transition-base;

  svg {
    transition: $transition-base;
  }

  &:hover {
    background: rgb(43 174 102 / 10%);

    svg {
      color: $secondary-color;
    }
  }

  &:active {
    transform: scale(0.9);
  }
}

.selected-date {
  flex: 1;
  color: $text-dark;
  font-family: "Fjalla One", sans-serif;
  font-size: 16px;
  font-weight: 700;
  text-align: center;
}

/* Nav 탭 섹션 */
.cont-nav {
  padding: 16px 24px;
  background: $bg-white;
  border-bottom: 1px solid $border-light;

  ul {
    display: flex;
    gap: 0;
    list-style: none;
    margin: 0;
    padding: 0;
  }

  li {
    position: relative;
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    padding: 12px 0;
    color: $text-light;
    font-family: "Noto Sans KR", Roboto, sans-serif;
    font-size: 12px;
    font-weight: 500;
    cursor: pointer;
    transition: $transition-base;

    &:hover {
      color: $primary-color;

      .nav-number {
        transform: scale(1.15);
      }
    }

    &.active {
      color: $primary-color;
      font-weight: 600;

      &::before {
        position: absolute;
        top: 5px;
        width: 3px;
        height: 3px;
        background-color: $primary-color;
        border-radius: 50%;
        content: "";
      }

      .nav-number {
        transform: scale(1.05);
      }
    }
  }
}

.nav-number {
  font-size: 12px;
  font-weight: 600;
  margin-top: 8px;
  transition: $transition-base;
}

/* 하단 네비게이션 */
.nav {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 999;
  display: flex;
  justify-content: center;
  padding: 17px 0 calc(17px + env(safe-area-inset-bottom, 0));
  background: rgb(255 255 255 / 98%);
  backdrop-filter: blur(20px);
  border-top: 1px solid rgb(0 0 0 / 8%);
  box-shadow: 0 -4px 20px rgb(0 0 0 / 8%);
}

.nav-first {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  padding: 0 20px;
  max-width: 760px;

  svg {
    width: 24px;
    height: 24px;
    color: $text-light;
    cursor: pointer;
    transition: $transition-base;

    &:hover {
      color: $primary-color;
      transform: scale(1.15);
    }

    &:active {
      transform: scale(1.05);
    }
  }
}

.nav-icon-bold {
  filter: drop-shadow(0 0 0.3px currentcolor);

  path {
    paint-order: stroke fill;
    stroke: currentcolor;
    stroke-width: 10;
  }
}

.feather-home {
  color: $primary-color;
}

/* btn-add 버튼 스타일 */
.btn-add {
  position: relative;
  cursor: pointer;

  &:active {
    color: $primary-color !important;
  }
}

.btn-favorite {
  .ico-default-path {
    display: block;
  }

  .ico-default-active {
    display: none;
  }

  &:active,
  &:hover {
    svg {
      fill: #ff6b9d;
      color: #ff6b9d;
    }

    .ico-default-path {
      display: none;
    }

    .ico-default-active {
      display: block;
    }
  }
}

/* 팝업 오버레이 */
.popup-overlay {
  position: absolute;
  z-index: 100;
  background: rgb(255 255 255 / 0.05%);
  animation: fade-in 0.2s ease;
  backdrop-filter: blur(3px);
  inset: 0;
  will-change: opacity;
  backface-visibility: hidden;
  contain: layout style paint;
  pointer-events: auto;

  &.closing {
    animation: fade-out 0.3s ease;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fade-out {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

/* 하단 슬라이드 팝업 (Bottom Sheet) */
.bottom-sheet {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 101;
  width: 100%;
  background: $bg-white;
  border-radius: $border-radius-lg $border-radius-lg 0 0;
  overflow: hidden;
  animation: slide-up 0.3s cubic-bezier(0.32, 0.72, 0, 1);
  box-shadow: 0 -4px 40px rgb(0 0 0 / 20%);
  max-height: 80vh;
  padding-bottom: 60px;
  will-change: transform;
  backface-visibility: hidden;
  transform: translateZ(0);
  contain: layout style paint;

  &.closing {
    animation: slide-down 0.3s cubic-bezier(0.32, 0.72, 0, 1);
  }
}

@keyframes slide-up {
  from {
    opacity: 0;
    transform: translateY(20%);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slide-down {
  from {
    opacity: 1;
    transform: translateY(0);
  }

  to {
    opacity: 0;
    transform: translateY(20%);
  }
}

@keyframes slide-up-desktop {
  from {
    opacity: 0;
    transform: translateY(20%);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slide-down-desktop {
  from {
    opacity: 1;
    transform: translateY(0);
  }

  to {
    opacity: 0;
    transform: translateY(20%);
  }
}

.bottom-sheet-handle {
  width: 40px;
  height: 4px;
  margin: 12px auto 8px;
  background: #d0d0d0;
  border-radius: 2px;
  cursor: grab;
}

.bottom-sheet-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 24px 16px;

  h2 {
    margin: 0;
    color: $text-dark;
    font-family: "Fjalla One", sans-serif;
    font-size: 20px;
    font-weight: 700;
  }
}

.bottom-sheet-content {
  max-height: calc(80vh - 120px);
  overflow-y: auto;
  padding: 24px;

  .empty-state {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 50px 0;
  }

  &.favorite-list {
    .favorite-group {
      margin-bottom: 32px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    .favorite-group-tag {
      display: flex;
      align-items: center;
      margin: 0 0 16px;
      font-size: 18px;
      font-weight: 600;
    }

    .favorite-group-tag-txt {
      position: relative;
      display: flex;
      align-items: center;
      gap: 8px;

      .tag-name {
        z-index: 8;
      }
    }

    .favorite-items {
      display: flex;
      flex-direction: column;
      gap: 12px;
    }

    .favorite-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 12px 16px;
      background: rgb(0 0 0 / 2%);
      border-radius: 8px;
      transition: $transition-base;

