          text-decoration: line-through;
          opacity: 0.5;
        }
      }
    }

    .favorite-item-text {
      flex: 1;
      font-size: 12px;
      line-height: 1.5;
    }

    .favorite-item-date {
      margin-left: 12px;
      color: $text-light;
      font-size: 11px;
    }
