/* React DatePicker 커스텀 스타일 */
.react-datepicker-wrapper {
  width: 100%;
}

.react-datepicker__input-container {
  width: 100%;
}

.react-datepicker__triangle {
  display: none;
}

.custom-popper {
  z-index: 250 !important;
}

.custom-calendar {
  border: 1px solid $border-light !important;
  border-radius: $border-radius-md !important;
  font-family: "Noto Sans KR", Roboto, sans-serif !important;
  box-shadow: 0 8px 24px rgb(0 0 0 / 15%) !important;

  .react-datepicker__header {
    position: relative !important;
    background-color: $bg-white !important;
    border-bottom: 1px solid $border-light !important;
    padding-top: 16px !important;
  }

  .react-datepicker__current-month {
    color: $text-dark !important;
    font-size: 14px !important;
    font-weight: 500 !important;
    margin-bottom: 8px !important;
    line-height: 24px !important;
  }

  .react-datepicker__day-name,
  .react-datepicker__day {
    display: inline-block !important;
    width: 2rem !important;
    margin: 0.166rem !important;
    line-height: 2rem !important;
    text-align: center !important;
  }

  .react-datepicker__day-name {
    color: $text-medium !important;
    font-size: 12px !important;
    font-weight: 500 !important;

    /* 일요일 빨간색 */
    &:first-child {
      color: #ec0000 !important;
    }

    /* 토요일 파란색 */
    &:last-child {
      color: #05f !important;
    }
  }

  .react-datepicker__week {
    .react-datepicker__day:first-child {
      color: #ec0000 !important;

      &:hover {
        color: #ec0000 !important;
      }

      /* 선택된 일요일 */
      &.react-datepicker__day--selected {
        color: #ec0000 !important;

        &:hover {
          color: #ec0000 !important;
        }
      }

      /* 키보드로 선택한 일요일 */
      &.react-datepicker__day--keyboard-selected {
        color: #ec0000 !important;

        &:hover {
          color: #ec0000 !important;
        }
      }

      /* 오늘이 일요일 */
      &.react-datepicker__day--today {
        color: #ec0000 !important;
        border-color: #ec0000 !important;

        &:hover {
          color: #ec0000 !important;
          border-color: #ec0000 !important;
        }
      }

      /* 다른 달의 일요일 */
      &.react-datepicker__day--outside-month {
        color: rgb(236 0 0 / 40%) !important;
      }
    }

    /* 주의 마지막 날 = 토요일 */
    .react-datepicker__day:last-child {
      color: #05f !important;

      &:hover {
        color: #05f !important;
      }

      /* 선택된 토요일 */
      &.react-datepicker__day--selected {
        color: #05f !important;

        &:hover {
          color: #05f !important;
        }
      }

      /* 키보드로 선택한 토요일 */
      &.react-datepicker__day--keyboard-selected {
        color: #05f !important;

        &:hover {
          color: #05f !important;
        }
      }

      /* 오늘이 토요일 */
      &.react-datepicker__day--today {
        color: #05f !important;
        border-color: #05f !important;

        &:hover {
          color: #05f !important;
          border-color: #05f !important;
        }
      }

      /* 다른 달의 토요일 */
      &.react-datepicker__day--outside-month {
        color: rgb(0 85 255 / 40%) !important;
      }
    }
  }

  .react-datepicker__day {
    border-radius: 4px !important;
    color: $text-dark !important;
    font-size: 13px !important;
    font-weight: normal !important;
    transition: $transition-base !important;

    &:hover {
      background-color: #f8f8f8 !important;
    }

    /* 일요일 빨간색 (클래스가 있는 경우) */
    &--sunday {
      color: #ec0000 !important;

      &:hover {
        color: #ec0000 !important;
      }
    }

    /* 토요일 파란색 (클래스가 있는 경우) */
    &--saturday {
      color: #05f !important;

      &:hover {
        color: #05f !important;
      }
    }

    &--selected {
      background-color: rgb(43 174 102 / 10%) !important;
      font-weight: 600 !important;

      &:hover {
        background-color: #f8f8f8 !important;
      }
    }

    &--keyboard-selected {
      background-color: rgb(6 48 100 / 8%) !important;
      color: $text-dark !important;

      /* 키보드 선택 시 일요일도 빨간색 유지 */
      &.react-datepicker__day--sunday {
        color: #ec0000 !important;
      }

      /* 키보드 선택 시 토요일도 파란색 유지 */
      &.react-datepicker__day--saturday {
        color: #05f !important;
      }
    }

    &--today {
      border: 1px solid #e6e7e8 !important;
      border-radius: 4px !important;

      &:hover {
        background-color: rgb(43 174 102 / 10%) !important;
      }

      /* 오늘이 일요일이면 빨간색 유지 */
      &.react-datepicker__day--sunday {
        color: #ec0000 !important;
        border-color: #ec0000 !important;

        &:hover {
          color: #ec0000 !important;
          border-color: #ec0000 !important;
        }
      }

      /* 오늘이 토요일이면 파란색 유지 */
      &.react-datepicker__day--saturday {
        color: #05f !important;
        border-color: #05f !important;

        &:hover {
          color: #05f !important;
          border-color: #05f !important;
        }
      }
    }

    &--outside-month {
      color: $text-light !important;
      opacity: 0.5;

      /* 다른 달의 일요일도 빨간색 (연하게) */
      &.react-datepicker__day--sunday {
        color: rgb(198 48 63 / 40%) !important;
      }

      /* 다른 달의 토요일도 파란색 (연하게) */
      &.react-datepicker__day--saturday {
        color: rgb(0 85 255 / 40%) !important;
      }
    }
  }

  .react-datepicker__navigation {
    top: 19px !important;
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    width: 24px !important;
    height: 24px !important;

    &--previous {
      left: 10px !important;
      border-right-color: $text-medium !important;

      &:hover {
        border-right-color: $primary-color !important;
      }
    }

    &--next {
      right: 10px !important;
      border-left-color: $text-medium !important;

      &:hover {
        border-left-color: $primary-color !important;
      }
    }
  }

  .react-datepicker__navigation-icon {
    width: 8px !important;
    height: 8px !important;

    &::before {
      border-color: #2c3e50 !important;
      border-width: 2px 2px 0 0 !important;
      top: 0 !important;
      width: 8px !important;
      height: 8px !important;
    }

    &:hover::before {
      border-color: #2c3e50 !important;
    }
  }
}

