/* Select 컴포넌트 */
.select-wrapper {
  position: relative;
  width: 100%;
}

.select-display {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 14px 16px;
  background: $bg-white;
  border: 1px solid $input-color;
  border-radius: $border-radius-sm;
  color: $text-dark;
  font-family: "Noto Sans KR", Roboto, sans-serif;
  font-size: 12px;
  cursor: pointer;
  transition: $transition-base;

  &:focus {
    background: $bg-white;
    border-color: $border-dark;
    outline: none;
  }

  &:hover {
    background: $bg-white;
  }

  &.type_filter {
    padding: 9px 0;
    background: transparent;
    border: none;
    outline: none;
    box-shadow: none;
    font-size: 12px;

    &:hover,
    &:focus {
      background: transparent;
      border: none;
      outline: none;
      box-shadow: none;
    }

    .select-arrow {
      margin: 2px 0 0 4px;
    }

    +.select-dropdown {
      left: auto;
    }
  }
}

.select-value {
  flex: 1;
  color: $text-dark;

  &.placeholder {
    color: $text-light;
  }
}

.select-arrow {
  flex-shrink: 0;
  margin-left: 8px;
  color: $text-medium;
  transition: transform 0.2s ease;

  &.open {
    transform: rotate(180deg);
  }
}

.select-dropdown {
  position: absolute;
  top: calc(100% + 4px);
  right: 0;
  left: 0;
  z-index: 250;
  margin: 0;
  padding: 0;
  background: $bg-white;
  border: 1px solid $input-color;
  border-radius: $border-radius-sm;
  max-height: 200px;
  overflow-y: auto;
  list-style: none;
  box-shadow: 0 4px 12px rgb(0 0 0 / 15%);
  min-width: 140px;

  li {
    margin: 0;
    padding: 0;
  }
}

.select-option {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 12px 16px;
  background: transparent;
  border: none;
  color: $text-dark;
  font-family: "Noto Sans KR", Roboto, sans-serif;
  font-size: 11px;
  text-align: left;
  cursor: pointer;
  transition: $transition-base;

  svg {
    flex-shrink: 0;
    margin-left: 8px;
    color: $primary-color;
  }

  &:hover,
  &.focused {
    background: rgb(6 48 100 / 4%);
    color: $primary-color;
  }

  &:active {
    background: rgb(6 48 100 / 8%);
  }

  &.selected {
    color: $primary-color;
    font-weight: 500;
  }
}

.popup-actions {
  display: flex;
  gap: 12px;
  margin-top: 24px;
}

