/* 투두 리스트 */
.cont-todolist {
  position: relative;
  flex: 1;
  padding: 32px 12px 60px;
  background-color: #fff;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  transform: translateZ(0);
  will-change: scroll-position;

  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-track {
    background: rgb(200 200 200 / 10%);
    border-radius: 10px;
  }

  &::-webkit-scrollbar-thumb {
    background: rgb(200 200 200 / 30%);
    border-radius: 10px;

    &:hover {
      background: rgb(200 200 200 / 50%);
    }
  }

  &.memo {
    background-image:
      // 왼쪽 여백 세로 줄
      linear-gradient(to right,
        transparent $margin-left - 1px,
        $margin-line-color $margin-left - 1px,
        $margin-line-color $margin-left + 1px,
        transparent $margin-left + 1px),
      // 가로 줄무늬
      repeating-linear-gradient(transparent,
        transparent $line-height - 1px,
        $line-color $line-height - 1px,
        $line-color $line-height );
    background-size:
      100% 100%,
      100% $line-height;
  }

  &.note {
    background-image: repeating-linear-gradient(transparent,
        transparent $line-height - 1px,
        $line-color $line-height - 1px,
        $line-color $line-height );
    background-size: 100% $line-height;
  }

  &.grid {
    background-image:
      // 세로 격자 줄
      repeating-linear-gradient(to right,
        transparent,
        transparent $grid-size - 1px,
        $grid-color $grid-size - 1px,
        $grid-color $grid-size ),
      // 가로 격자 줄
      repeating-linear-gradient(to bottom,
        transparent,
        transparent $grid-size - 1px,
        $grid-color $grid-size - 1px,
        $grid-color $grid-size );
    background-size: $grid-size $grid-size;
  }
}

.empty-state {
  width: 100%;
  padding: 150px 20px 40px;
  color: #9d9d9d;
  font-size: 14px;
  font-weight: 600;
  text-align: center;
}

/* 태그별 그룹 */
.todo-group {
  &+& {
    margin-top: 48px;
  }
}

.todo-items-grid {
  display: grid;
  gap: 0;
  grid-template-columns: 1fr;

  /* PC에서 2열 그리드 */
  @media (width >=769px) {
    grid-template-columns: repeat(2, 1fr);
    gap: 0 8px;
  }
}

.todo-group-tag {
  position: relative;
  display: flex;
  align-items: center;
  gap: 8px;
  margin: 16px 0 8px 18px;
  padding: 0;
  color: $text-dark;
  font-family: "Fjalla One", sans-serif;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 0.5px;
}

.todo-group-tag-txt {
  position: relative;

  .tag-name {
    position: relative;
    z-index: 9;
  }
}


/* 태그 색상 박스 */
.tag-color-box {
  position: absolute;
  bottom: 0;
  left: 3px;
  z-index: 8;
  width: 100%;
  height: 8px;
  border-radius: 2px;
  opacity: 0.5;
  flex-shrink: 0;

  &.small {
    width: 10px;
    height: 10px;
    border-radius: 2px;
  }
}

/* 투두 아이템 */
.todo-item {
  position: relative;
  display: flex;
  align-items: center;
  gap: 13px;
  padding: 3px 18px;
  border-radius: 16px;
  font-family: "Noto Sans KR", Roboto, sans-serif;
  transition: all .3s ease;

  &:last-child {
    border-bottom: none;
  }

  &.completed {
    opacity: 0.65;

    .todo-text {
      color: $text-gray;
      text-decoration: line-through;
    }

    .drag-handle {
      opacity: 0 !important;
      pointer-events: none;
    }
  }

  &.draggable {
    cursor: move;

    &:active {
      opacity: 0.5;
      cursor: grabbing;
    }
  }

  &.is-dragging {
    opacity: 0.5;
    box-shadow: 0 5px 15px rgb(0 0 0 / 20%);
  }

  &:hover {
    .todo-actions {
      opacity: 1;
    }

    &:not(.completed) .drag-handle {
      opacity: 1;
    }
  }

  /* 텍스트가 긴 경우 전체 너비 사용 */
  &.todo-item-long {
    @media (width >=769px) {
      grid-column: 1 / -1;
    }
  }
}

.drag-handle {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 4px;
  color: $text-medium;
  opacity: 0;
  cursor: grab;
  transition: $transition-base;

  svg {
    display: block;
  }

  &:active {
    cursor: grabbing;
  }
}


.todo-actions {
  display: flex;
  gap: 6px;
  transition: opacity 0.3s ease;
}


.todo-checkbox {
  position: relative;
  width: 18px;
  height: 18px;
  background: $bg-white;
  opacity: 0.001;
  cursor: pointer;
  transition: $transition-base;
  accent-color: $bg-white;
  appearance: none;
  outline: 0;
  border-color: transparent;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 18px;
    height: 18px;
    box-sizing: border-box;
    border: 2px solid $primary-color;
    border-radius: 4px;
  }

  &:hover {
    border-color: $primary-color;
  }

  &:checked {
    background: $primary-color;
  }
}

.todo-content {
  display: flex;
  flex: 1;
  flex-direction: column;
  padding-left: 2px;
  gap: 4px;
}

.todo-text {
  color: $text-dark;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5;
  cursor: default;
  overflow-wrap: break-word;
}

.todo-action-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 28px;
  height: 34px;
  border: none;
  color: $text-medium;
  font-size: 14px;
  cursor: pointer;
  transition: $transition-base;

  svg {
    color: $text-medium;
  }

  &:hover {
    transform: scale(1.1);
  }

  &:disabled {
    opacity: 0.3;
    cursor: not-allowed;

    &:hover {
      transform: none;
    }
  }

  &.favorite {
    &.active {
      svg {
        color: #ff6b9d;
        fill: #ff6b9d;
      }
    }

    &:hover:not(:disabled) svg {
      color: #ff6b9d;
    }

    &:disabled {
      svg {
        color: $text-medium;
        fill: none;
      }

      &.active svg {
        fill: $text-medium;
      }
    }
  }
}

/* 로그아웃 메뉴 아이템 */
.logout-item {
  margin-top: 12px;
  padding-top: 12px;
  color: #ff4757;

  &:hover {
    background: rgb(255 71 87 / 5%);
  }
}